<template>
  <div class="collect">
      <div class="head">
          <div class="fa fa-close" @click="$router.go(-1)"></div>
          <div>收藏</div>
      </div>
      <div class="list">
          <div class="song" @click="play(v.id)" v-for="(v,i) in newSongList" :key="i">
          <Musiclist @ :id="v.id" :authorarr="v.author" :songname="v.name"></Musiclist>
          </div>
      </div>
      <div v-if="newSongList.length == 0" style="padding:16px">
          暂时没有收藏歌曲，快去收藏一首试试看吧
      </div>
  </div>
</template>

<script>
import Musiclist from './../components/Musiclist'
export default {
    data () {
        return {
            SongList:[],
            data:null,
        }
    },
    computed:{
        newSongList(){
            return this.SongList.sort((a,b)=>{
                return a.rang - b.rang
            })
        }
    },
    created(){
        
       this.set_data();
        
    },
    methods:{
        play(id){
            // this.$root.$children[0].isPlay =true;
            this.$root.$children[0].currentPlayId= id;
            // console.log(id);
        },
        set_data(){
            let arr = this.$root.$children[0].collectList;
        // console.log('collect',arr);
        let newArr = [];
        let Obj = {};
        arr.forEach(v=>{
            Obj[v] = 0;
        })
        for( let i in Obj){
            newArr.push(i);
        }
        this.$root.$children[0].collectList = newArr;
        
        if(newArr.length  > 0){

            this.$root.$children[0].collectList.forEach((v,i)=>{
                this.axios("/song/detail?ids="+v).then(data => {
                    
                    this.SongList.push({
                        rang:i,
                        name:data.data.songs[0].name,
                        author:data.data.songs[0].ar,
                        id:data.data.songs[0].id,
                    })
                })
            })
        }

        }
    },
    watch:{

    },
    components:{
        Musiclist
    }
}
</script>

<style lang="scss">
#app > .collect{
    height: 100%;
    overflow: auto;
    .head{
        display: flex;
        padding: 20px 16px;
        div:nth-child(2){
            flex: 1;
            text-align: center;
        }
        border-bottom: 3px solid skyblue;
    }
    .list{
        padding: 16px;
    }
}
</style>